import { preferences as Preference } from 'mercadopago';

const api = {
  payment: {
    async create(cart) {
      try {
        // Creamos la preferencia incluyendo los productos del carrito
        const preference = await new Preference().create({
          body: {
            items: cart.map(item => ({
              id: item.id.toString(), // ID del producto
              title: item.name,       // Nombre del producto
              unit_price: item.price, // Precio unitario
              quantity: item.quantity // Cantidad
            })),
            metadata: {
              cartDetails: cart // Guardamos los detalles del carrito en los metadatos
            }
          }
        });

        // Devolvemos el init_point (URL de pago) para que el usuario pueda pagar
        return preference.init_point;
      } catch (error) {
        console.error('Error al crear la preferencia de pago:', error);
        throw new Error('No se pudo generar la preferencia de pago');
      }
    }
  }
};

export default api;
